html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

input {
  color: #FFFFFF;
  outline: none;
}

img {
  max-width: 100%;
}

body {
  background: #252222;
  font-family: "Circe", sans-serif;
  font-weight: 400;
}

h4 {
  margin: 0;
  padding: 0;
  list-style: none;
}

h3 {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: "Circe";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 120%;
}

h2 {
  margin: 0;
  padding: 0;
  font-size: 36px;
}

section {
  padding-bottom: 80px;
  padding-top: 80px;
}

.flex {
  flex-direction: flex;
}

.conteiner {
  max-width: 1290px;
  margin: 0 auto;
}

@font-face {
  font-family: "Circe";
  src: local("Circe-Regular"), url("../src/static/fonts/Circe/Circe-Regular.woff2") format("woff2"), url("../src/static/fonts/Circe/Circe-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Circe";
  src: local("Circe-Regular"), url("../src/static/fonts/Circe/Circe-Bold.woff2") format("woff2"), url("../src/static/fonts/Circe/Circe-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Circe";
  src: local("Circe-Regular"), url("../src/static/fonts/Circe/Circe-Light.woff2") format("woff2"), url("../src/static/fonts/Circe/Circe-Light.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rubik One";
  src: local("RubikOne-Regular"), url("../src/static/fonts/RubikOne/RubikOne-Regular.woff2") format("woff2"), url("../src/static/fonts/RubikOne/RubikOne-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
/* header */
.header-top {
  max-height: 126px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.05);
  padding-bottom: 8px;
}

.header-top__wrap {
  display: flex;
  align-items: center;
}

.header-top-logo {
  margin-right: auto;
}

.header-top-right {
  display: flex;
}

.header-top-right__btn {
  font-family: "Circe";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: #FFFFFF;
}

.header-top-right__login {
  width: 190px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #252222;
}

.header-top-right__login:hover {
  background-color: #0B972A;
}

.header-top-right__subscribe {
  width: 190px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #0B972A;
  margin-right: 30px;
  background: #252222;
  cursor: pointer;
}

.header-top-right__subscribe:hover {
  background-color: #0B972A;
}

.heder-top-right-menu-hidden {
  display: none;
}

/* header-nav */
.header-nav__wrap {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.header-nav-menu__list {
  display: flex;
  margin-top: 30px;
}

.header-nav-menu__item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  border-right: 2px solid rgba(255, 255, 255, 0.05);
  padding-right: 30px;
  padding-left: 30px;
  font-family: "Circe";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  color: #FFFFFF;
}

.header-nav-menu__item:hover {
  text-decoration: underline;
}

.header-nav-menu__item:active {
  color: #0B972A;
}

.header-nav-menu__item:first-child {
  padding-left: 0px;
}

.header-nav-menu__item:last-child {
  border-right: 0px;
  padding-right: 0px;
}

.header-nav-search {
  margin-top: 22px;
}

.header-nav-search__btn-hidden {
  display: none;
}

.header-nav-search__form {
  border: none;
  display: flex;
  position: relative;
}

.header-nav-search__input {
  width: 410px;
  box-sizing: border-box;
  padding: 17px 55px;
  height: 60px;
  background-color: #181717;
  border: none;
  color: #FFFFFF;
}

.header-nav-search__input:focus::placeholder {
  opacity: 0;
}

.header-nav-search__btn {
  background: url("../src/static/img/icon/search.png");
  width: 20px;
  height: 20px;
  position: absolute;
  margin: 20px;
  opacity: 0.3;
}

/* slider */
.slaider {
  padding-top: 0px;
}

.slaider__wrap {
  box-sizing: border-box;
  display: flex;
  max-width: 1290px;
  max-height: 438px;
  background-color: #181717;
  position: relative;
}

.slaider__image {
  width: 630px;
  height: 438px;
  background-image: url("../src/static/img/machu-picchu.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.slider-content__wrap {
  max-width: 660px;
}

.slider-content__title {
  font-family: "Rubik One";
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 120%;
  color: #FFFFFF;
}

.slider-content__discription {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  margin-left: 30px;
  max-width: 436px;
}

.slider-content__category {
  width: 145px;
  height: 40px;
  background-color: #252222;
  color: #0B972A;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}

.slider-content__text {
  border-left: 4px solid #0B972A;
  padding-left: 30px;
  font-family: "Circe";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  max-width: 401px;
}

.slider-content-btn div {
  display: flex;
}

.slider-content-btn a {
  display: block;
  padding-left: 30px;
  margin-right: 40px;
}

.slider-content-btn {
  display: flex;
  justify-content: space-between;
  margin-top: 86px;
  margin-left: 30px;
  align-items: center;
  color: #FFFFFF;
}

.slider-content-btn__like {
  display: block;
  width: 16px;
  height: 18px;
  cursor: pointer;
  background: url("../src/static/img/icon/wpf_like (1).png") no-repeat;
  background-position: start;
}

.slider-content-btn:hover {
  background: url("../src/static/img/icon/wpf_like.png") no-repeat;
}

.slider-content-btn__comment {
  display: block;
  width: 20px;
  height: 20px;
  cursor: pointer;
  background: url("../src/static/img/icon/comment.svg") no-repeat;
  background-position: start;
}

.slider-content-btn__comment:hover {
  background: url("../src/static/img/icon/fluent_comment-24-filled.svg") no-repeat;
  color: #0B972A;
}

.slider-content-btn__read {
  background-color: #0B972A;
  width: 190px;
  height: 60px;
  position: absolute;
  right: 0px;
  top: 86%;
  color: #FFFFFF;
}

.slider-pagination-list {
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.slider-pagination-list__item {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: #181717;
}

.slider-pagination-list__item:first-child {
  background-color: #0B972A;
}

/* last-story */
.last-story {
  padding-top: 0px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.05);
}

.last-story-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.last-story-title__show-all {
  color: #0B972A;
  background: url("../src/static/img/icon/Frame 24.png") no-repeat;
  padding-right: 20px;
  padding-top: 10px;
  display: block;
  background-position: 95%;
}

.last-story-title__show-all:hover {
  background-position: 100%;
}

.last-story-title h2 {
  font-family: "Rubik One";
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 120%;
  color: #FFFFFF;
}

.last-story-list {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 30px;
  grid-template-areas: "first second third" "first fourth fifth";
}

.last-story-list__item:nth-child(1) {
  grid-area: first;
}

.last-story-list__item:nth-child(2) {
  grid-area: second;
}

.last-story-list__item:nth-child(3) {
  grid-area: third;
}

.last-story-list__item:nth-child(4) {
  grid-area: fourth;
}

.last-story-list__item:nth-child(5) {
  grid-area: fifth;
}

.last-story-list__item:first-child {
  max-width: 630px;
  max-height: 488px;
  background: #181717;
  height: 100%;
}

.last-story-card-head {
  position: relative;
}

.last-story-card-head__link {
  display: inline-block;
  height: 285px;
  max-width: 630px;
  overflow: hidden;
}

.last-story-card-head__link > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.3s ease-in-out;
}

.last-story-card-head__link:hover img {
  transform: scale(1.1);
}

.last-story-card-head-bottom__link {
  display: block;
  height: 203px;
  padding-top: 30px;
  margin-left: 20px;
}

.last-story-card-head-bottom__wrapper h3 {
  max-width: 405px;
  font-family: "Circe";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 120%;
  color: #FFFFFF;
}

.last-story-card-head-bottom__wrapper p {
  max-width: 425px;
  max-height: 78px;
  border-left: 4px solid #0B972A;
  padding-left: 20px;
  font-family: "Circe";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
}

.last-story-card-head__category {
  width: 145px;
  height: 32px;
  background-color: #252222;
  color: #0B972A;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 54%;
}

.last-story-card {
  position: relative;
}

.last-story-card__link {
  display: inline-block;
  height: 136px;
  overflow: hidden;
}

.last-story-card__link > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.3s ease-in-out;
}

.last-story-card__link:hover img {
  transform: scale(1.2);
}

.last-story-card-bottom__link {
  display: block;
  height: 94px;
}

.last-story-card__category {
  width: 125px;
  height: 30px;
  background-color: #252222;
  color: #0B972A;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 52%;
}

.last-story-card-head__category {
  font-family: "Circe";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 120%;
}

.last-story-card__category {
  font-family: "Circe";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 120%;
}

.last-story-list__item {
  max-width: 300px;
  max-height: 230px;
  background-color: #181717;
  height: 100%;
}

.last-story-card-bottom__wrapper {
  padding-top: 25px;
  max-width: 275px;
  padding-left: 13px;
}

.last-story-card-bottom__wrapper h4 {
  font-family: "Circe";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 120%;
  color: #FFFFFF;
}

.last-story-card-head__wrapper {
  max-width: 445px;
  height: 203px;
}

.last-story-card-head-bottom__img {
  width: 30px;
  height: 30px;
  background: url("../src/static/img/icon/Vector 3.png") no-repeat;
  position: absolute;
  right: 4px;
  top: 21px;
}

.last-story-card-head-bottom__arrow {
  position: absolute;
  display: flex;
  height: 60px;
  width: 60px;
  background-color: #0B972A;
  right: 0px;
  bottom: 5px;
}

.last-story-card-head-bottom__img:hover {
  right: -3px;
}

