  align-items: center;
  justify-content: space-between;
  margin-bottom: 35px;
}

.podcasts-title__title {
  font-family: "Rubik One";
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 120%;
  color: #FFFFFF;
}

.podcasts-title__show-all {
  color: #0B972A;
  background: url("../src/static/img/icon/Frame 24.png") no-repeat;
  padding-right: 20px;
  padding-top: 10px;
  display: block;
  background-position: 95%;
}

.podcasts-title__show-all:hover {
  background-position: 100%;
}

.podcasts-title-card__link {
  font-family: "Circe";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 120%;
  color: #FFFFFF;
}

.podcasts-list {
  display: grid;
